  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  display: flex;
  justify-content: center; }
  .header .header-city {
    background-image: url(//michelle-pine.github.io/portfolio-2020/static/media/city.389cafe9.svg);
    position: absolute;
    bottom: 0;
    background-size: auto 33vh;
    background-position: left bottom;
    background-repeat: repeat-x;
    left: 0;
    margin-bottom: -5px;
    height: 50vh;
    min-height: 50vh;
    width: 100vw; }
  .header .header-logo {
    width: 250px;
    z-index: 99; }
  .header .down-button {
    position: absolute;
    bottom: 30px;
    left: auto;
    right: auto;
    z-index: 99;
    text-rendering: auto;
    color: #E9275B;
    font-size: 33px;
    background: none;
    border: none;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: center;
    cursor: pointer;
    -webkit-transition: 0.3s all;
    transition: 0.3s all; }
